export class BunniesLog {

    rounds : Round[] = [];

    addNewRound( oprimalMoves: number){
        this.rounds.push(new Round(oprimalMoves))
    }
    
    addJump(){
        this.rounds[this.rounds.length-1].moves++;
    }
}

export class Round {
    
    moves = 0;
    
    constructor(public optimalMoves: number) {}
}